<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="reportCategoryTableData" :noDataText="reportCategoryTableNoDataText" :sst="true" search @search="filterReportCategoryTable" @sort="orderReportCategoryTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadReportCategoryTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="reportCategoryTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ reportCategoryTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="reportCategoryTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="categoryId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="categoryName">
            {{ this.$t('CATEGORY_NAME') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].categoryId">
              {{ data[index].categoryId }}
            </vs-td>
            <vs-td :data="data[index].categoryName">
              {{ data[index].categoryName }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="reportCategoryTableTotalPages" v-model="reportCategoryTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import ReportCategoryTableMixin from '@/mixins/report/ReportCategoryTableMixin'

export default {
  name: 'report-category-popup',
  mixins: [PopupMixin, ReportCategoryTableMixin],
  mounted: function () {
    this.loadReportCategoryTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
